<template>
  <div class="weather">
    <h1>天氣預報</h1>
    <div class="input-and-select">
      <input type="text" name="" id="" placeholder="輸入城市名稱" class="inputCity" @keyup.enter="selectWeather()" v-model="weather.city">
      <div class="select" @click="selectWeather()">
        <div>查詢</div>
      </div>
    </div>
    <div class="info" v-if="weather.weatherInfo">
      <h3 class="cityName">{{ weather.weatherInfo.locationName }} 的天氣</h3>
      <div class="preview">
        <ul>
          <li v-for="i in [0, 1, 2]" :key="i">
            <div class="time">時間：{{ weather.weatherInfo.weatherElement[0].time[i].startTime.slice(5, 16) }} 至 {{ weather.weatherInfo.weatherElement[0].time[i].endTime.slice(5, 16) }}</div>
            <div class="minT">溫度範圍：{{ weather.weatherInfo.weatherElement[2].time[i].parameter.parameterName }}°{{ weather.weatherInfo.weatherElement[2].time[i].parameter.parameterUnit }}</div> ~
            <div class="maxT">{{ weather.weatherInfo.weatherElement[4].time[i].parameter.parameterName }}°{{ weather.weatherInfo.weatherElement[4].time[i].parameter.parameterUnit }}</div>
            <div class="ci">舒適度：{{ weather.weatherInfo.weatherElement[3].time[i].parameter.parameterName }}</div>
            <div class="pop">降雨機率：{{ weather.weatherInfo.weatherElement[1].time[i].parameter.parameterName }}%</div>
          </li>
        </ul>
      </div>
    </div>
    <div v-if="weather.status" class="not-found">
      <svg viewBox="0 0 48 48" fill="none" xmlns="http://www.w3.org/2000/svg"><g id="SVGRepo_bgCarrier" stroke-width="0"></g>
        <g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g>
        <g id="SVGRepo_iconCarrier"> 
          <path fill-rule="evenodd" clip-rule="evenodd" d="M24 42C33.9411 42 42 33.9411 42 24C42 14.0589 33.9411 6 24 6C14.0589 6 6 14.0589 6 24C6 33.9411 14.0589 42 24 42ZM24 44C35.0457 44 44 35.0457 44 24C44 12.9543 35.0457 4 24 4C12.9543 4 4 12.9543 4 24C4 35.0457 12.9543 44 24 44Z" fill="red"></path> 
          <path fill-rule="evenodd" clip-rule="evenodd" d="M16.2218 31.7782C15.8313 31.3877 15.8313 30.7545 16.2218 30.364L30.364 16.2218C30.7545 15.8313 31.3877 15.8313 31.7782 16.2218C32.1687 16.6124 32.1687 17.2455 31.7782 17.636L17.6361 31.7782C17.2455 32.1687 16.6124 32.1687 16.2218 31.7782Z" fill="red"></path> 
          <path fill-rule="evenodd" clip-rule="evenodd" d="M16.2218 16.2218C16.6124 15.8313 17.2455 15.8313 17.636 16.2218L31.7782 30.364C32.1687 30.7545 32.1687 31.3876 31.7782 31.7782C31.3877 32.1687 30.7545 32.1687 30.364 31.7782L16.2218 17.636C15.8313 17.2455 15.8313 16.6123 16.2218 16.2218Z" fill="red"></path> 
        </g>
      </svg>
      <h3>{{ weather.status }}</h3>
    </div>
  </div>
</template>

<script setup>
  import { reactive } from 'vue';
  import axios from 'axios';

  const weather = reactive({
    city: '',
    weatherInfo: null,
    status: '',
  })
  const apiKey = 'CWA-9FE3582B-CDD1-44E9-A325-5166BF82BA9E'


  const selectWeather = async() => {
    if (weather.city) {
      const url = `https://opendata.cwa.gov.tw/api/v1/rest/datastore/F-C0032-001?Authorization=${apiKey}&locationName=${weather.city}`
      try {
        const res = await axios.get(url)
        if (res.data.records.location.length > 0) {
          weather.weatherInfo = res.data.records.location[0]
          weather.status = null
          console.log(weather.weatherInfo);
        } 
        else {
          console.error('未找到該城市的天氣數據')
          weather.status = '未找到該城市的天氣數據'
          weather.weatherInfo = null
        }
      } 
      catch (error) {
        console.error('抓取天氣資料失敗', error);
        weather.status = '抓取天氣資料失敗'
      }
    }
    weather.city = ''
  }


</script>

